import React from "react";

export type ProductTermsType = {
    title: string;
    terms: { before?: string; data: string; after?: string };
    explanation: string;
};

const Item: React.FC<{ item: ProductTermsType }> = ({ item }) => {
    return (
        <div className="last:pl-20 mobile:last:pl-0 mobile:last:mt-6">
            <span className="text-lg mobile:text-xs font-medium mobile:font-semibold">
                {item.title}
            </span>
            <h4 className="font-bold font-secondary whitespace-nowrap">
                {item.terms.before && (
                    <span className="text-4xl mobile:text-2xl leading-[42px] mobile:leading-7">
                        {item.terms.before}&nbsp;
                    </span>
                )}
                <span className="text-[54px] leading-[64px] mobile:text-[34px] mobile:leading-[39px] static">
                    {item.terms.data}
                </span>
                &nbsp;
                <span className="text-4xl mobile:text-2xl leading-[42px] mobile:leading-7">
                    {item.terms.after}
                </span>
            </h4>
            <p className="mt-2 mobile:text-[10px] text-sm">
                {item.explanation}
            </p>
        </div>
    );
};

export const ProductTerms: React.FC<{ data: Array<ProductTermsType> }> = ({
    data,
}) => {
    return (
        <div className="flex mobile:flex-col mt-10 mobile:mt-4">
            {data.map((item) => (
                <Item key={item.title} item={item} />
            ))}
        </div>
    );
};
